import React from 'react';
import { Link } from 'gatsby';

interface Post {
  id: string;
  frontmatter: {
    title: string;
    date: string;
    description?: string;
  };
  fields: {
    slug: string;
  };
}

interface PostListProps {
  posts: Post[];
  limit?: number; 
}

export const PostList: React.FC<PostListProps> = ({ posts, limit }) => {
  const displayedPosts = limit ? posts.slice(0, limit) : posts;

  return (
    <div className="flex flex-col gap-6">
      {displayedPosts.map((post) => (
        <article
          key={post.id}
          className="flex flex-col pb-4"
        >
          <Link
            to={`/blog/${post.fields.slug}`}
            className="text-content dark:text-neutral-50 text-xl font-semibold mb-4 hover:text-highlight"
          >
            {post.frontmatter.title}
          </Link>
          <time
            dateTime={new Date(post.frontmatter.date).toISOString()}
            className="text-sm text-neutral-500"
          >
            {new Date(post.frontmatter.date).toLocaleDateString(undefined, {
              day: '2-digit',
              month: 'short',
              year: 'numeric',
            })}
          </time>
          {/* {post.frontmatter.description && (
            <p className="mt-2 text-neutral-400">{post.frontmatter.description}</p>
          )} */}
        </article>
      ))}
    </div>
  );
};
